body{

  padding: 50px;
  background-color: #ccfff5;
  font-family: sans-serif;
}



#container{
  display: flex;
  justify-content: flex;
}

.navbar { /* Men√º oben fixed */
  overflow: hidden;
  position: fixed;
  top: 0;

  width: 88%;
}

.navbar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 22px 22px;
  text-decoration: none;
  font-size: 18px;
  font-family: fantasy;
  color:#ffccff;
}


.navbar a:hover {
  background:#ffccff;
  color: black;
  border-radius: 20px;
  border-color: black;
}



